
body {
  margin: 0px;
}
#app {
  font-family: 'Lato';
  color: #644D52;
}
@media (max-width: 768px) {
.slider {
    width: 50%;
}
}
@media (min-width: 768px) {
.slider {
    width: 240px;
}
}
.fa {
  cursor: pointer;
}
.menu {
  display: inline;
}
.menu select, .menu input {
  padding: 5px;
  margin: 5px;
  border-style: solid;
  border-width: thin;
  border-radius: 5px;
  border-color: #F77A52;
  color: #F77A52;
  font-weight: bold;
}
.description {
  max-width: 720px;
  margin: 0 auto;
  text-align: left;
}
.description {
  padding: 40px 0px;
}



