
.fade {
  position: fixed;
  z-index: 900;
  top:0px; bottom: 0px; left: 0px; right: 0px;
  background-color: rgba(0,0,0,0.7);
}
.lightbox {
  position: fixed;
  z-index: 1000;
  top:20%; bottom: 20%; left: 10px; right: 10px;
  background-color: black;
  background-color: #ffffff;
  overflow-y: scroll;
}
.lightbox h1 {
  padding: 20px 10px 10px;
}
ul {
  padding: 0;
  list-style-type: none;
}
.scorePanel {
  width: 40%;
  float: left;
}
.centerPanel {
  width: 20%;
  float: left;
}
.scorePanel h3 {
  /*width: 200px;*/
  text-align: center;
}
.scoreSection {
  float: left;
}
@media (min-width: 601px) {
.testScores {
    width: 70%;
}
.gpaScores {
    width:30%;
}
}
.scoresWrap {
  max-width: 600px;
  margin: 0 auto;
}
@media (max-width: 600px) {
.testScores {
    display: block;
    width: 100%;
}
.gpaScores {
    display: block;
    width:100%;
}
}
.fa-window-close {
  position: fixed;
  top: 21%;
  right: 30px;
  font-size: 40px;
  z-index: 2000;
}
.lightbox span {
  display: inline-block;
  text-align: center;
}
.gradeHeader {
  width: 100px;
}
.gradeNumber {
  width: 50px;
}



