
#graph {
}
.graphCanvas {
  width:720px;
  height: 240px;
}
rect.safety, rect.reach {
  fill: #644D52;
}
rect.target {
  fill: #F77A52;
}
rect:hover{
  stroke-opacity:0.4;
  stroke-width:3;
  stroke-linejoin: round;
}
rect.target:hover{
  stroke:#F77A52;
}
rect.safety:hover, rect.reach:hover{
  stroke:#644D52;
}
.label {
  font-size: 4px;
  font-family: 'Lato';
}
.schoolLabel {
  font-size: 5px;
}
.schoolLabel:hover {
  font-weight: bold;
}
@media (max-width: 660px) {
.label:not(.scoreLabel) {
    transform: scale(0.65,1)
}
}
.schoolLabel.target {
  text-decoration: underline;
  fill: #FF974F;
}
.scoreLine {
  stroke: #644D52;
  stroke-width: 0.5;
  opacity: 0.8;
}
@media (max-width:767.9px) {
.graphCanvas {
    width:100%;
}
.maxLabel, .minLabel, .scoreLabel {
    display: none
}
}
.schoolLabel, rect.target, rect.safety, rect.reach {
  cursor: pointer;
}



