
#myHeader {
  top:0px;
  left:0px;
  right: 0px;
  background-color: #644D52;
  color: white;
  margin-bottom: 40px;
}
@media (max-width: 768px) {
.body {
    padding-left: 10px;
    padding-right: 10px;
}
}
@media (max-width: 835px) {
#myHeader, #myFooter {
    padding: 20px 10px;
}
}
@media (min-width: 835px) {
#myHeader, #myFooter {
    padding:20px 20px 20px 0px;
    text-align: center;
}
h1 {
    display: inline-block;
    padding-left: 40px;
    padding-right: 40px;
    border-right: thin solid white;
}
h1:last-of-type {
    border: none;
}
#myHeader h3 {
    padding-left: 40px;
}
}



